import { ImageSize } from "../utils/constants";

export type ImageType = keyof typeof ImageSize;

export type ImageRatio = "2/3" | "1/1" | "16/9";

export type Media = {
  title: string;
  id: number;
  backdrop_path: string;
  poster_path: string;
  vote_average: number;
  release_date: string;
};

export enum MediaType {
  Movie = "movie",
  Tv = "tv",
  Person = "person",
}

export enum MovieType {
  Popular = "popular",
  Upcoming = "upcoming",
}

export type Genre = {
  id: number;
  name: string;
};

export type Genres = Genre[];

export type State = {};

export interface AppContext extends State {
  dispatch: React.Dispatch<any>;
}

export interface Credits {
  cast: Cast[];
  crew: Cast[];
}

interface Cast {
  adult: boolean;
  gender: number;
  id: number;
  known_for_department: string;
  name: string;
  original_name: string;
  popularity: number;
  profile_path: null | string;
  cast_id?: number;
  character?: string;
  credit_id: string;
  order?: number;
  department?: string;
  job?: string;
}

export type OriginalLanguage = {
  [key: string]: string;
};

export type OriginCountry = {
  [key: string]: string;
};

export interface ProductionCountry {
  iso_3166_1: string;
  name: string;
}

export interface SpokenLanguage {
  english_name: string;
  iso_639_1: string;
  name: string;
}

export interface Videos {
  results: VideosResult[];
}

interface VideosResult {
  iso_639_1: OriginalLanguage;
  iso_3166_1: string;
  name: string;
  key: string;
  site: string;
  size: number;
  type: string;
  official: boolean;
  published_at: string;
  id: string;
}
